@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&family=Source+Sans+Pro:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;700&display=swap");
body {
  background-color: #f3f4ed;
}

#headerNav {
  margin-bottom: 5px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.logoHeader {
  margin: 5px;
}

.logoHeader h1 {
  border-bottom: solid 3px #c06014;
  font-family: "Quicksand", sans-serif;
  letter-spacing: 3px;
  font-weight: 500;
  font-size: calc(1.375rem + 1.5vw);
  color: #424642;
  text-transform: uppercase;
  margin-bottom: 8px;
  line-height: 1.2;
}

li {
  list-style: none;
  display: inline-block;
  margin-right: 20px;
}

a {
  color: black;
  text-decoration: none;
  padding: 8px;
  font-weight: 400;
}

a:hover {
  color: #c06014;
}

p {
  margin: 5px;
  font-family: "Quicksand", sans-serif;
  font-weight: lighter;
  color: #424642;
  text-align: left;
}

#headerListHamburger {
  display: none;
}

@media (max-width: 620px) {
  #headerList {
    display: none;
  }
  #headerListHamburger {
    display: inline-block;
  }
  #headerListHamburger svg:hover {
    cursor: pointer;
  }
  #headerListHamburger button {
    border: none;
    background-color: #f3f4ed;
  }
  #headerListHamburger button:hover {
    background: rgba(192, 96, 20, 0.2);
    border-radius: 50%;
  }
  #headerListHamburger li {
    display: block;
    background: rgba(66, 70, 66, 0.6);
    padding: 5px;
  }
  #headerListHamburger li:hover {
    background: rgba(66, 70, 66, 0.8);
  }
  #headerListHamburger li a {
    color: white;
  }
  #headerListHamburger #hamDiv {
    position: absolute;
    border-radius: 50px;
  }
}
