#explore {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  overflow-x: hidden;
}

#mapDiv {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 700px;
}

#map svg {
  scale: 2;
}

#map #OH, #map #IN, #map #IL, #map #MI, #map #MN, #map #ND, #map #SD, #map #MO, #map #KS, #map #IA, #map #NE, #map #WI {
  fill: #aaaaaa;
}

#map #OH:hover, #map #IN:hover, #map #IL:hover, #map #MI:hover, #map #MN:hover, #map #ND:hover, #map #SD:hover, #map #MO:hover, #map #KS:hover, #map #IA:hover, #map #NE:hover, #map #WI:hover {
  cursor: pointer;
}

#map #OH:hover {
  fill: #432771;
}

#map #IN:hover {
  fill: #f7bc17;
}

#map #IL:hover {
  fill: #53b35a;
}

#map #MO:hover {
  fill: #1aa9a9;
}

#map #MI:hover {
  fill: #625d59;
}

#map #KS:hover {
  fill: #e9565e;
}

#map #NE:hover {
  fill: #a3ce65;
}

#map #IA:hover {
  fill: #8f7d71;
}

#map #SD:hover {
  fill: #f7bc17;
}

#map #WI:hover {
  fill: #e3595e;
}

#map #ND:hover {
  fill: #246cb6;
}

#map #MN:hover {
  fill: #7f4f99;
}
